import {
	afterSaleDetail
} from "../../../api/user.js"
export default {
	data() {
		return {
			id: '', // 售后id
			record: {}
		}
	},
	onLoad(ops) {
		uni.hideShareMenu()
		this.id = ops.id
		this.getDetail()
	},
	methods: {
		getDetail() {
			uni.showLoading()
			afterSaleDetail({
				data: {
					id: this.id
				}
			}).then(res => {
				uni.hideLoading()
				if (res.data.code == 0) {
					this.record = res.data.result
				}
			})
		}
	}
}
